export class Operation {
    public creationDateOperation: number
    public intituleOperation: string
    public codeOperation: string
    public serviceContractant: string
    public montantOperation: number
    public yearPlanAction: number
    public pourLaStructureRattachement: string

    constructor(
        creationDateOperation: number,
        intituleOperation: string,
        codeOperation: string,
        serviceContractant: string,
        montantOperation: number,
        yearPlanAction: number,
        pourLaStructureRattachement: string
        ){
            this.creationDateOperation = creationDateOperation,
            this.intituleOperation=intituleOperation,
            this.codeOperation=codeOperation,
            this.serviceContractant=serviceContractant,
            this.montantOperation=montantOperation,
            this.yearPlanAction=yearPlanAction,
            this.pourLaStructureRattachement=pourLaStructureRattachement
    }
  }