import { Component, OnInit } from '@angular/core';
import { CreateOperationComponent } from '../dialogs/create-operation/create-operation.component';
import { MatDialog } from '@angular/material';
import { Operation } from '../models/operation.model';
import { Projet } from '../models/projet.model';
import { NaturePrestation } from '../enums/nature.prestation.enum';
import { CreateProjectComponent } from '../dialogs/create-project/create-project.component';


@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.css']
})
export class ProjectsComponent implements OnInit {

  operations: Array<Operation> = []
  projectsMap: Map<string, Array<Projet>> = new Map() ;
  
  constructor(public dialog: MatDialog) { }

  ngOnInit() {
  }

  
  openDialogCreateOperation(): void {
    const dialogRef = this.dialog.open(CreateOperationComponent, {
      width: '800px',
      data: "serviceContractant"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    const item: Operation = new Operation (
      Date.now(),
      "Opration d'acquisition Matriel Informatique",
      Math.random().toString(36).substring(7),
      "Direction Finances et comptabilite (DFC)",
      50,
      2002,
      "structureX"
      ); 

    this.operations.push(item)
    this.operations.sort((n1,n2) => n2.creationDateOperation - n1.creationDateOperation);

  }

  openDialogCreateProject(operation: Operation): void {

    console.log(operation.codeOperation)

    const key = operation.codeOperation

    const item: Projet = new Projet(
      Date.now(),
      "This a cool project",
      Math.random().toString(36).substring(7),
      NaturePrestation.Etudes,
      key,
      5000,
      5000
    )

    if (this.projectsMap.has(key)){
      const projects = this.projectsMap.get(operation.codeOperation);
      projects.push(item);
    } else {
      const projects: Array<Projet> = []
      projects.push(item)
      this.projectsMap.set(key,projects)
    }
      
    

    const dialogRef = this.dialog.open(CreateProjectComponent, {
      width: '800px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  onOperationCreated(operation: Operation): void{
    this.operations.push(operation);
  }
}
