export class MiseAuPoint {
    dateDemande: Date
    dateMiseAuPoint: Date
    PVPath: string 
    resume: string 

    constructor(
        dateDemande: Date,
        dateMiseAuPoint: Date,
        PVPath: string,
        resume: string 
    ) {
        this.dateDemande=dateDemande,
        this.dateMiseAuPoint=dateMiseAuPoint,
        this.PVPath=PVPath,
        this.resume=resume
    }

    static getDummy(): MiseAuPoint {
        return new MiseAuPoint(
            new Date(),
            new Date(),
            "PVPath",
            "resume"
        )
    }
}