import { NaturePrestation } from '../enums/nature.prestation.enum'

export class Projet {
    public creationDateProject: number
    public intituleProjet: string
    public codeProjet: string
    public naturePrestation: NaturePrestation
    public codeOperation: string
    public montantProjet: number
    public estimationAdministrative: number
    constructor(
        creationDateProject: number,
        intituleProjet: string,
        codeProjet: string,
        naturePrestation: NaturePrestation,
        codeOperation: string,
        montantProjet: number,
        estimationAdministrative: number
    ){
        this.creationDateProject = creationDateProject
        this.intituleProjet = intituleProjet
        this.codeProjet = codeProjet
        this.naturePrestation = naturePrestation
        this.codeOperation = codeOperation
        this.montantProjet = montantProjet
        this.estimationAdministrative = estimationAdministrative
    }
  }