import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { CreateServiceContractantComponent } from '../dialogs/create-service-contractant/create-service-contractant.component';

@Component({
  selector: 'app-services-contractants',
  templateUrl: './services-contractants.component.html',
  styleUrls: ['./services-contractants.component.css']
})
export class ServicesContractantsComponent implements OnInit {

  constructor(public dialog: MatDialog) {}

  ngOnInit() {
  }
  
  openDialogCreateServiceContractant(): void {
    const dialogRef = this.dialog.open(CreateServiceContractantComponent, {
      width: '800px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog CreateServiceContractantComponent was closed');
    });
  }



}
